const duration = 2000;
import http from '@/components/utils/http.js';
import { mapState, mapMutations } from 'vuex';

export default {
	components: {},
	data() {
		return {
			codeBut: '获取验证码',
			codeClick: true,
			mobile: '',
			password: '',
			repassword: '',
			code: '',
			backpage: '../index/home',
			Code:'',
			
		};
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['logout']),
		
		// 密码输入
		passwordInpur(e) {
			this.password = e.detail.value;
		},
		// 重复密码输入
		repasswordInpur(e) {
			this.repassword = e.detail.value;
		},
		// 验证码输入
		codeInput(e) {
			this.code = e.detail.value;
		},
		// 获取验证码
		getCode() {
			var _that = this;

			if (_that.codeClick) {
					_that.codeClick = false;
					// 发送短信
					_that._sendmsm();
					// 倒计时开始
			}
		},
		// 点击修改
		repass() {
			var _that = this;
			if (_that.password == '') {
				this.$api.msg('请输入密码');
				return;
			} else if (_that.repassword != _that.password) {
				this.$api.msg('两次密码输入不一致');
				return;
			} else if (_that.code == '') {
				this.$api.msg('请输入验证码');
				return;
			} else {
				const data = {
					password: _that.password,
					repassword: _that.repassword,
					code: _that.code,
				};
				this._regrequest(data);
			}
		},

		_regrequest(userdata) {
			let _this=this;
			let opts = {
				url: '/base/repass/',
				method: 'post'
			};
			let param = userdata;
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						_this.logout();
						uni.navigateTo({
							url: "./logon"
						});
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		_sendmsm() {
			var _that = this;
			let opts = {
				url: '/base/sendmsm/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						var daojishi = 60;
						var xianshi = setInterval(function() {
							_that.codeBut = '重新发送' + daojishi;
							daojishi--;
							if (daojishi <= 0) {
								_that.codeBut = '重新发送';
								_that.codeClick = true;
								clearInterval(xianshi);
							}
						}, 1000);
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete => {
					this.loading = false;
				}
			);
		},
		
		
		
	},
	onReady() {},
	onLoad(options) {
		
		
		
	}
};
